window.addEventListener("scroll", () => {
	// Load
	let	progresses = document.querySelectorAll("#js-progress-whom-01, #js-progress-whom-02, #js-progress-whom-03"),
		textCard = document.querySelectorAll("#js-text-01, #js-text-02, #js-text-03"),
		card = document.querySelectorAll("#js-card-01, #js-card-02, #js-card-03");
		
	progresses.forEach((item, i) => {
		if (isElementInViewport(item)) {
			item.style.animation = "progress 2s forwards";
			card[i].style.animation = "expand-card 1s forwards";
			let id = setTimeout(() => {
				textCard[i].style.display = "block";
				textCard[i].style.animation = "opacity 3s forwards";
			}, 1000);
		}
	});
		
	function isElementInViewport(elem) {
		let rect = elem.getBoundingClientRect();

		return (
			(rect.top >= 0) &&
			(rect.bottom <= (window.innerHeight || document.documentElement.clientHeight)) 
		);
	}
});

window.addEventListener("resize", () => {
	// Buy
	let payment = document.getElementById("js-payment");
	
	if (window.innerWidth >= 1367) {
		cardElems()[0].forEach((item, i) => {
			if (!(item.classList.contains("products__card_active"))) {
				item.classList.add("products__card_active");
				cardElems()[1][i].classList.add("products__title_active");
				cardElems()[2][i].classList.add("products__descr_active");
			}
		});
	}
	
	payDim();
});